@use "global" as *;

.more-button a,
.search-button button,
.alllist-button button,
.send-button button {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: $second-font;
  margin: 0 auto;
  padding: rem(6) rem(44);
  font-size: rem(32);
  background: #fff;
  border: 0.5px solid #333;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  text-decoration: none;
  width: rem(250);
  height: rem(68);
  text-align: center;
  outline: none;
  transition: ease 0.2s;
  white-space: nowrap;

  @include mq(sp) {
    font-size: rem(24);
    width: rem(200);
    height: rem(48);
    line-height: 1.4;
  }
}

.search-button button {
  color: #333;
  text-decoration: none;
  opacity: 0.5;
  padding: rem(10) rem(30);
  line-height: normal;
  width: 100%;
  max-width: rem(160);
  font-size: rem(24);
  box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.25);

  @include mq(sp) {
    max-width: rem(155);
  }

  &:hover {
    background: #888;
    color: $white;
  }

  &:disabled {
    color: $main;
    opacity: 0.5;
    cursor: not-allowed;
  }
}

.send-button button {
  color: #333;
  text-decoration: none;
  opacity: 0.5;
  padding: rem(10) rem(30);
  line-height: normal;
  max-width: rem(250);
  font-size: rem(32);

  @include mq(sp) {
    max-width: rem(155);
  }

  &:hover {
    background: #888;
    color: $white;
  }

  &:disabled {
    color: $main;
    opacity: 0.5;
    cursor: not-allowed;
  }
}

.alllist-button.type-2 button {
  border: none;
  padding: rem(6) rem(80);
  background: #d9d9d9;
  color: #333;

  @include mq(sp) {
    padding: rem(6) rem(64);
    font-size: rem(24);
    max-width: rem(200);
    line-height: 1.4;
  }
}

.more-button a span,
.alllist-button button span {
  position: relative;
  z-index: 3;
  color: #333;
}

.more-button a:hover span {
  color: #fff;
}

.alllist-button a:hover span {
  color: #d9d9d9;
}

.bgskew::before {
  content: "";
  position: absolute;
  top: 0;
  left: -130%;
  background: #666;
  width: 120%;
  height: 100%;
  transform: skewX(-25deg);
}

.bgskew:hover::before {
  animation: skewanime 0.5s forwards;
}

@keyframes skewanime {
  100% {
    left: -10%;
  }
}
