@use "global" as *;

.top-gallery {
  background: $bg;
  padding-block: rem(160);

  @include mq(sp) {
    padding-block: rem(80);
  }
}

.top-gallery__inner {
  max-width: rem(1145);
  width: 100%;
  margin: 0 auto;
  padding-inline: rem(40);

  @include mq(sp-ss) {
    padding-inline: rem(20);
  }

  @include mq(sp-xs) {
    padding-inline: rem(10);
  }
}

.gallery-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(rem(335), 1fr));
  gap: rem(30);

  @include mq(sp-xs) {
    grid-template-columns: repeat(auto-fit, minmax(rem(300), 1fr));
  }
}

.gallery-card {
  background: white;
  box-shadow: 2px 2px 4px 0px rgba(51, 51, 51, 0.5);
  opacity: 0;
  cursor: pointer;
  width: 100%;
  max-width: rem(600);
  max-height: rem(314);
  margin-inline: auto;
  padding: rem(30) rem(20);
  outline: 1px solid #333;
  display: flex;
  flex-direction: column;

  a {
    height: 70%;
  }
}

.gallery-card__img {
  aspect-ratio: 8/7;
  display: block;
  width: 100%;
  height: 90%;
}

.gallery-card__wrap {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: rem(20);
  gap: rem(20);
}

.gallery-card__icon {
  width: rem(80);
  height: rem(80);
  border-radius: 50%;
}

.gallery-card__info {
  display: flex;
  flex-direction: column;
  gap: rem(10);
}

.gallery-card__name {
  font-size: rem(16);
  line-height: 1.25;
  color: #333;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.gallery-card__type {
  font-size: rem(16);
  color: #333;
}

.more-button {
  margin-top: rem(80);
}
